.wrapper {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}
.left_block {
    width: 70%;
    margin: 0 auto;
}

.block_text {
    display: flex;
}

.block_number {
    color: #FFBF2B;
    font-size: 26px;
    font-weight: bold;
    margin-top: 15px;
}

.text {
    margin: 15px 0 15px 15px;
}

.underline {
    width: 100%;
    border: none;
    height: 2px;
    color: rgb(199, 196, 196);
    background-color: rgb(199, 196, 196);
    
}

.right_block {
    background: url('../../img/laptop.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    width: 130%;
    height: 300px;
}

.block_header {
    font-size: 30px;
    font-weight: normal;
    text-align: center;
    width: 80%;
    margin: 20px auto;
}


@media (min-width: 1280px) {

    .block_header {
        font-size: 40px;
        text-align: left;
        width: 100%;
    }

.wrapper {
    width: 70%;
    justify-content: space-between;
    margin-bottom: 30px;
    margin: 0 auto;
    flex-wrap: nowrap;
}
.left_block {
    width: 50%;
}

.right_block {
    width: 50%;
    background: url('../../img/laptop.jpg');
    background-repeat: no-repeat;
    margin: 20px;
    background-size: 100% auto;
}

}